<template>
  <div class="index">
    <div style="background: #3e5873;height: 56px">
      <span style="color: white;font-size: 2em;line-height: 56px;float: left;margin-left: 20px">Realm Browser</span>
      <Button style="margin-top: 12px;float: right;margin-right: 20px" type="primary" @click="saveApiUrl">确定</Button>
      <Input v-model="apiUrl" placeholder="请输入 api 地址"
             style="float:right;width: 300px;height:56px;line-height: 56px;margin-right: 10px"></Input>
    </div>
    <Row type="flex" style="height: calc(100% - 56px)">
      <i-col span="4">
        <Menu theme="light" @on-select="handleSelect" width="100%" style="height: 100%">
          <Menu-item v-for="table in tableList" :name="table.name" :key="table">{{ table.name }}</Menu-item>
        </Menu>
      </i-col>
      <i-col span="20" style="background: whitesmoke">
        <template v-if="tableColumns.length!==0">
          <Table border :columns="tableColumns" :data="tableData"></Table>
        </template>
      </i-col>
    </Row>
  </div>
</template>
<script>
  import ICol from '../../node_modules/iview/src/components/grid/col';
  let KEY_API_URL = 'API_URL';
  export default {
    components: {ICol},
    data () {
      return {
        activeTable: '',
        tableList: [],
        tableColumns: [],
        tableData: [],
        apiUrl: ''
      };
    },
    computed: {},
    created () {
      this.getTables();
      this.apiUrl = this.getApiUrl();
    },
    methods: {
      getApiUrl () {
        let vm = this;
        let apiUrl = vm.$localStorage.get(KEY_API_URL);
        if (!apiUrl) {
          return 'http://10.0.0.114:3421';
        } else {
          if (apiUrl.charAt(apiUrl.length - 1) === '/') {
            apiUrl = apiUrl.substring(0, apiUrl.length - 1)
          }
          return apiUrl;
        }
      },
      saveApiUrl () {
        let vm = this;
        vm.$localStorage.set(KEY_API_URL, vm.apiUrl);
        console.log(this.apiUrl);
        vm.getTables();
      },
      handleSelect (name){
        console.log(name);
        this.activeTable = name;
        this.getTableColumns(name);
      },
      getTables () {
        let vm = this;
        this.$nextTick(function () {
          vm.axios.get(vm.getApiUrl() + '/table/list').then((response) => {
            vm.tableList = response.data;
          });
        });
      },
      getTableColumns (tableName) {
        this.tableColumns.length = 0;
        this.tableData.length = 0;
        let baseUrl = this.getApiUrl();
        this.axios.get(baseUrl + '/table/' + tableName).then((response) => {
          let tableFields = response.data;
          tableFields.map(item => {
            let column = {
              title: item.field_name + ' (' + item.type.toLowerCase() + ')',
              key: item.field_name,
              width: 200,
            };
            this.tableColumns.push(column);
          });
        });
        this.axios.get(baseUrl + '/data/' + tableName).then((response) => {
          response.data.map(item => {
            this.tableData.push(item);
          });
        });
      }
    }
  };
</script>

<style scoped>
  .index {
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    /*text-align: center;*/
  }

  .index h1 {
    height: 150px;
  }

  .index h1 img {
    height: 100%;
  }

  .index h2 {
    color: #666;
    margin-bottom: 200px;
  }

  .index h2 p {
    margin: 0 0 50px;
  }

  .index .ivu-row-flex {
    height: 100%;
  }
</style>



